import helpers from '@/helpers/localStorageHelper';
<template>
  <div class="new-model-container row">
    <div class="new-model-title">配置新模型</div>
    <div class="create-name row">
      <span class="label">创建名称</span>
      <el-input class="create-name-input" v-model="newModelName" placeholder="请输入模型名称"></el-input>
    </div>
    <div class="model-model row">
      <span class="label">模型模板</span>
      <div class="model-model-container" ref="imgContentContainer">
        <div class="model-model-content row" @mousewheel="mousewheel" ref="imgContent">
          <div class="img-container row" v-for="(item, index) in modelModel" :class="{'active': activeModelModel===item}" :key="index" @click="handleImgClick(item)">
            <div class="title">{{item}}</div>
            <img class="model-img" :src="require(`@/assets/img/${item}.webp`)">
          </div>
        </div>
        <div class="left-button model-model-content-button row" @click="wheelImgContent(false)">
          <i class="el-icon-caret-left i"></i>
        </div>
        <div class="right-button model-model-content-button row" @click="wheelImgContent(true)">
          <i class="el-icon-caret-right i"></i>
        </div>
      </div>
    </div>
    <div class="para-container row">
      <div class="left row">
        <div class="para-content row">
          <div class="label">层数</div>
          <el-input v-model="level"></el-input>
        </div>
        <div class="para-content row">
          <div class="label">加入通道注意力</div>
          <el-dropdown trigger="click" @command="handleChannelAttentionClick">
            <span class="el-dropdown-link">
              {{channelAttention}} <i class="el-icon-caret-bottom"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="true">True</el-dropdown-item>
              <el-dropdown-item :command="false">False</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="para-content row">
          <div class="label">加入空间注意力</div>
          <el-dropdown trigger="click" @command="handleSpaceAttentionClick">
            <span class="el-dropdown-link">
              {{spaceAttention}} <i class="el-icon-caret-bottom"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item :command="true">True</el-dropdown-item>
              <el-dropdown-item :command="false">False</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="para-content row">
          <div class="label">激活函数</div>
          <el-dropdown trigger="click" @command="handleActiveFunctionClick">
            <span class="el-dropdown-link">
              {{activeFunction}} <i class="el-icon-caret-bottom"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="ReLU">ReLU</el-dropdown-item>
              <el-dropdown-item command="Swish">Swish</el-dropdown-item>
              <el-dropdown-item command="h-swish">h-swish</el-dropdown-item>
              <el-dropdown-item command="ReLU6">ReLU6</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
      <div class="right row">
        <div class="para-content row">
          <div class="label">学习率</div>
          <el-input v-model="studyRatio"></el-input>
        </div>
        <div class="para-content row">
          <div class="label">最大轮数</div>
          <el-input v-model="maxTurn"></el-input>
        </div>
        <div class="para-content row">
          <div class="label">批尺寸</div>
          <el-input v-model="batchSize"></el-input>
        </div>
        <div class="para-content row">
          <div class="label">优化器</div>
          <el-dropdown trigger="click" @command="handleOptimizerClick">
            <span class="el-dropdown-link">
              {{optimizer}} <i class="el-icon-caret-bottom"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="Adam">Adam</el-dropdown-item>
              <el-dropdown-item command="SGD">SGD</el-dropdown-item>
              <el-dropdown-item command="RMSProp">RMSProp</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="bottom row">
      <el-button @click="startTraining">
        <span>开始训练</span>
      </el-button>
    </div>
  </div>
</template>

<script>
import helpers from '@/helpers/localStorageHelper';
import scrollAnimation from '@/helpers/animationHelper';

export default {
  data() {
    return {
      activeModelModel: 'DenseNet',
      scrollWidth: 0,
      imgContentWidth: 0,
      modelModel: ['DenseNet', 'FishNet', 'PixelAggregationNetwork', 'PVANet'],
      level: 101,
      channelAttention: true,
      spaceAttention: true,
      activeFunction: 'Relu',
      studyRatio: '0.01',
      maxTurn: '40000',
      batchSize: 64,
      optimizer: 'Adam',
      newModelName: '',
      step: 250,
      ifInAnimation: false
    };
  },
  watch: {
    scrollWidth(val, oldVal) {
      this.ifInAnimation = true;
      // eslint-disable-next-line
      new scrollAnimation(
        oldVal,
        val,
        100,
        current => this.$refs.imgContent.style.left = `-${current}px`,
        () => this.ifInAnimation = false
      );
    }
  },
  methods: {
    handleChannelAttentionClick(e) {
      this.channelAttention = e;
    },
    handleSpaceAttentionClick(e) {
      this.spaceAttention = e;
    },
    handleActiveFunctionClick(e) {
      this.activeFunction = e;
    },
    handleOptimizerClick(e) {
      this.optimizer = e;
    },
    startTraining() {
      if (!this.newModelName) {
        this.$message.error('请输入模型名称');
        return;
      }
      const data = {
        modelName: this.newModelName,
        modelModel: this.activeModelModel,
        level: this.level,
        channelAttention: this.channelAttention,
        spaceAttention: this.spaceAttention,
        activeFunction: this.activeFunction,
        studyRatio: this.studyRatio,
        maxTurn: this.maxTurn,
        batchSize: this.batchSize,
        optimizer: this.optimizer
      };
      let trainingModels = helpers.getItem('trainingModels');
      trainingModels = trainingModels ? JSON.parse(helpers.getItem('trainingModels')) : [];
      if (Object.prototype.toString.call(trainingModels) === '[object Array]') {
        if (trainingModels.some(item => item.modelName === this.newModelName)) {
          this.$message.error('模型名称已存在');
          return;
        }
        trainingModels.push(data);
        helpers.setItem('trainingModels', JSON.stringify(trainingModels));
      } else {
        this.$message.error('已存在的训练中模型列表格式检查未通过');
        return;
      }
      this.$message.success('已将模型加入训练列表');
    },
    handleImgClick(item) {
      this.activeModelModel = item;
    },
    wheelImgContent(flag) {
      if (flag) {
        if (this.imgContentWidth - (this.scrollWidth + this.step) < this.$refs.imgContentContainer.clientWidth) {
          this.scrollWidth = this.imgContentWidth - this.$refs.imgContentContainer.clientWidth;
        } else {
          this.scrollWidth += this.step;
        }
      } else {
        this.scrollWidth = this.scrollWidth - this.step < 0 ? 0 : this.scrollWidth - this.step;
      }
    },
    mousewheel(e) {
      if (!this.ifInAnimation) {
        this.wheelImgContent(e.deltaY > 0);
      }
    }
  },
  mounted() {
    this.imgContentWidth = this.$refs.imgContent.clientWidth;
  }
};
</script>

<style lang="scss" scoped>
.el-dropdown-menu.el-popper {
  color: #ffffff;
  border: 1px solid #ffffff1a;
  background-color: #18272f;
  .el-dropdown-menu__item {
    color: #ffffff;
  }
  .el-dropdown-menu__item:not(.is-disabled):hover {
    color: #43a9e6;
    background-color: #ffffff1a;
  }
  /deep/ .popper__arrow::after {
    border-bottom-color: #18272f;
  }
}
.el-input {
  /deep/ .el-input__inner {
    border: solid 1px rgba(255, 255, 255, 0.15);
    color: #ffffff;
    background-color: #080e14;
  }
}
.label {
  margin-right: 66px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  width: 56px;
}
.new-model-container {
  padding: 24px 40px;
  flex-direction: column;
  color: #ffffff;
  .new-model-title {
    margin-bottom: 38px;
    color: #ffffff;
    font-size: 24px;
  }
  .create-name {
    align-items: center;
    padding: 12px 0px;
    border-top: 1px solid #ffffff1a;
    .create-name-input {
      width: 183px;
      height: 40px;
    }
  }
  .model-model {
    height: 230px;
    border-top: 1px solid #ffffff1a;
    border-bottom: 1px solid #ffffff1a;
    .label {
      margin-top: 22px;
    }
    .model-model-container {
      width: calc(100vw - 410px);
      position: relative;
      overflow: hidden;
      .model-model-content {
        position: absolute;
        left: 0px;
        height: 100%;
        align-items: center;
        justify-content: space-between;
        // overflow: auto;
        .img-container {
          flex-direction: column;
          margin-right: 16px;
          padding: 18px;
          width: 302px;
          height: 206px;
          flex-shrink: 0;
          border-radius: 2px;
          background-color: #2e3a431f;
          cursor: pointer;
          &:hover {
            background-color: #2e3a4362;
          }
          &.active {
            padding-top: 16px;
            padding-left: 16px;
            border: solid 2px #ffffff;
          }
          .model-img {
            width: 264px;
            height: 150px;
            mix-blend-mode: screen;
          }
        }
      }
      .model-model-content-button {
        position: absolute;
        top: 0;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 230px;
        background-color: #21343de4;
        cursor: pointer;
        opacity: 0;
        &:hover {
          opacity: 1;
        }
        &.left-button {
          left: 0;
        }
        &.right-button {
          right: 0;
        }
      }
    }
  }
  .para-container {
    padding: 14px 0px;
    height: 196px;
    border-bottom: 1px solid #ffffff1a;
    .left, .right {
      flex-direction: column;
      justify-content: space-between;
      .para-content {
        align-items: center;
        .el-input {
          width: 182px;
          height: 40px;
        }
        .label {
          margin-right: 24px;
          width: 98px;
        }
        .el-dropdown-link {
          color: #ffffffb2;
          &:hover {
            color: #ffffff;
          }
        }
      }
    }
    .right {
      margin-left: 90px;
    }
  }
  .bottom {
    margin-top: 48px;
    justify-content: center;
    .el-button {
      color: #ffffffc8;
      border: 1px solid #ffffff1a;
      border-radius: 32px;
      background-color: #1494e0;
      &:hover {
        color: #ffffff;
        background-color: #3ab5ff;
      }
      &:active {
        background-color: #0074b9;
      }
    }
  }
}
</style>
