const RAF_FLAG = 'performance' in window && 'requestAnimationFrame' in window;

class RafAnimation {
  constructor(start, end, duration, cb, endCb) {
    this.start = start;
    this.end = end;
    this.duration = duration;
    this.flag = end > start;
    this.cb = cb;
    this.endCb = endCb;
    this.startTime = window.performance.now();
    this.stepByStep();
  }

  stepByStep() {
    let current = (window.performance.now() - this.startTime) / this.duration * (this.end - this.start) + this.start;
    current = this.flag ? Math.min(current, this.end) : Math.max(current, this.end);
    this.cb(current);
    if (current === this.end) {
      // eslint-disable-next-line
      typeof this.endCb === 'function' && this.endCb();
    } else {
      this.raf = window.requestAnimationFrame(() => {
        this.stepByStep();
      });
    }
  }

  stop() {
    window.cancelAnimationFrame(this.raf);
  }
}

class AnimationBySetTimeout {
  static stepDuration = 40;
  constructor(start, end, duration, cb, endCb) {
    this.flag = end - start > 0;
    this.value = start;
    this.stepTotalCount = Math.ceil(duration / AnimationBySetTimeout.stepDuration);// 20ms执行一次
    this.stepValue = (end - start) / this.stepTotalCount;
    this.end = end;
    this.timer = null;
    this.cb = cb;
    this.endCb = endCb;
  }
  start() { this.step(); }
  step() {
    if (this.value !== this.end) {
      this.value = this.flag ? Math.min(this.value + this.stepValue, this.end) : Math.max(this.value + this.stepValue, this.end);
      this.cb(this.value);
      this.timer = window.setTimeout(this.step.bind(this), AnimationBySetTimeout.stepDuration);
    } else {
      this.endCb();
    }
  }
  stop() { window.clearTimeout(this.timer); }
}

export default class ScrollAnimation {
  constructor(start, end, duration, cb, endCb) {
    this.instance = RAF_FLAG ? new RafAnimation(start, end, duration, cb, endCb) : new AnimationBySetTimeout(start, end, duration, cb, endCb);
  }
  stop() {
    this.instance.stop();
  }
}
