<template>
  <div class="row container">
    <div class="left row">
      <div class="title">{{onLineOrTraining?'已上线模型':'训练中模型'}}</div>
      <div class="left-content-sidebar">
        <el-menu
          v-if="ifHasModel"
          :default-active="activeModel"
          class="left-menu"
          background-color="transparent"
          text-color="#fff"
          @select="handleMenuSelect"
          active-text-color="#ffd04b">
          <el-menu-item v-for="(item, index) in models" class="menu-item-container row" :key="index" :index="`${index+1}`">
            <span slot="title" class="span row">{{item.modelName}}<i class="el-icon-caret-right icon-right"></i></span>
          </el-menu-item>
        </el-menu>
        <div class="none-content-container" v-else>
          <!-- 没有内容时展示 -->
        </div>
      </div>
    </div>
    <div class="right row">
      <model-view-detail
        @delete-model="deleteModel"
        :modelDetail="models[activeModel-1]?models[activeModel-1]:{}">
      </model-view-detail>
    </div>
  </div>
</template>

<script>
import helpers from '@/helpers/localStorageHelper';
import ModelViewDetail from '@/components/management/ModelViewDetail';
import api from '@/rest/modelRest';

export default {
  props: {
    onLineOrTraining: {
      required: true,
      type: Boolean
    }
  },
  data() {
    return {
      which: null,
      activeModel: '1',
      models: []
    };
  },
  async mounted() {
    this.which = this.onLineOrTraining ? 'onLineModels' : 'trainingModels';
    if (this.which === 'onLineModels') {
      this.models = await api.getOnlineModel();
    } else {
      this.models = JSON.parse(helpers.getItem(this.which) ? helpers.getItem(this.which) : '[]');
    }
  },
  computed: {
    ifHasModel() {
      return !!this.models.toString();
    }
  },
  methods: {
    deleteModel() {
      this.models.splice(this.activeModel - 1, 1);
      helpers.setItem(this.which, JSON.stringify(this.models));
      this.$message.success('删除成功');
    },
    handleMenuSelect(e) {
      this.activeModel = e;
    }
  },
  components: {
    ModelViewDetail
  }
};
</script>

<style lang="scss" scoped>
.container {
  color: #ffffff;
  .left {
    flex-direction: column;
    width: 440px;
    .title {
      padding: 30px 26px;
      font-size: 24px;
      color: #ffffff;
      border-bottom: 1px solid #ffffff1a;
    }
    .left-content-sidebar {
      .left-menu {
        border: none;
        .menu-item-container {
          align-items: center;
          height: 68px;
          color: #ffffffb2 !important;
          padding-left: 26px !important;
          border-bottom: 1px solid #ffffff1a;
          &:hover {
            color: #ffffff !important;
            background-color: transparent !important;
          }
          &.is-active {
            background-color: #ffffff1a !important;
          }
          .span {
            justify-content: space-between;
            align-items: center;
            width: 100%;
          }
          .icon-right {
            margin-left: 50px;
          }
        }
      }
      .none-content-container {
        padding: 30px 26px;
      }
    }
  }
  .right {
    flex: 1;
    flex-direction: column;
    background-color: #18272f;
  }
}
</style>
